<template>
  <container>
    <!--  -->
    <el-form :inline="true" :model="query" ref="query" class="goods_query_form">
      <el-form-item prop="id">
        <el-input v-model.number="query.id" placeholder="ID"/>
      </el-form-item>
      <el-form-item prop="status">
        <el-select v-model="query.type" placeholder="">
          <el-option label="" value="1"></el-option>
          <el-option label="" value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item prop="status">
        <el-select v-model="query.status" placeholder="">
          <el-option label="" value="1"></el-option>
          <el-option label="" value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" :icon="Search" @click="getMarketList"></el-button>
        <el-button type="primary" :icon="Brush" @click="resetForm"/>
        <el-button type="primary" :icon="Plus" @click="add"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-table :data="marketList" height="65vh" style="width: 100%">
     <el-table-column prop="bannerImage" label="" min-width="50">
        <template #default="scope">
          <el-image :src="scope.row.bannerImage" style="border-radius: 5px;"/>
        </template>
      </el-table-column>
      <el-table-column prop="name" label=""/>
      <el-table-column prop="type" label="" min-width="50">
        <template #default="scope">
          <el-tag v-if="scope.row.type === 1" type="primary"></el-tag>
          <el-tag v-if="scope.row.type === 2" type="primary"></el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="beginTime" label="" min-width="100">
        <template #default="scope">
          <div style="display: flex; align-items: center">
            <el-icon>
              <timer/>
            </el-icon>
            <span style="margin-left: 10px">{{ scope.row.beginTime }}</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="overTime" label="" min-width="100">
        <template #default="scope">
          <div style="display: flex; align-items: center">
            <el-icon>
              <timer/>
            </el-icon>
            <span style="margin-left: 10px">{{ scope.row.overTime }}</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="status" label="" min-width="50">
        <template #default="scope">
          <el-switch v-model="scope.row.status" :active-value="1" :inactive-value="2" @click="changeStatus(scope.row)"/>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template #default="scope">
          <el-button size="small" @click="edit(scope.row)"></el-button>
          <el-popconfirm title=", ?"
                         confirmButtonText=""
                         cancelButtonText=""
                         cancelButtonType="default"
                         :icon="WarningFilled"
                         @confirm="deleteMarket(scope.row)">
            <template #reference>
             <el-button size="small" type="danger"></el-button>
            </template>
          </el-popconfirm>
        </template>
      </el-table-column>
      <template #empty>
        <div style="margin: 50px 0;">
          <el-empty v-if="showEmpty" description="" />
        </div>
      </template>
    </el-table>
    <div style="padding: 10px 0;">
      <el-pagination layout="total, prev, pager, next"
                   :current-page="pageNum"
                   :page-size="pageSize"
                   :total="total"
                   @current-change="handleCurrentChange"
                   @prev-click="handleCurrentChangePrev"
                   @next-click="handleCurrentChangeNext" background/>
    </div>
    <!--  -->
    <el-dialog :title="dialogTitle" v-model="marketDialogVisible" :lock-scroll="false" top="5vh" width="45%"
               @close="cancel">
      <el-form ref="ruleForm" :rules="rules" :model="market" label-width="80px">
        <el-form-item label="" prop="name">
          <el-input v-model="market.name" type="text" maxlength="20" show-word-limit/>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model.number="market.type" placeholder="" clearable>
            <el-option v-for="item in typeOption" :key="item.value" :label="item.label" :value="item.value"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="bannerImage">
          <el-input v-show="false" v-model="market.bannerImage"/>
          <el-upload
              action="http://localhost:8000/web/upload"
              :headers="{'token': token}"
              :limit="1"
              name="file"
              :file-list="pictureList"
              list-type="picture-card"
              :on-preview="handleImagePreview"
              :on-remove="handleImageRemove"
              :on-success="handleImageSuccess">
            <div class="goods_image_upload_icon">+</div>
          </el-upload>
        </el-form-item>
        <el-form-item label="" prop="beginTime">
          <el-date-picker v-model="market.beginTime" value-format="YYYY-MM-DD HH:mm:ss" type="datetime" placeholder=""/>
        </el-form-item>
        <el-form-item label="" prop="overTime">
          <el-date-picker v-model="market.overTime" value-format="YYYY-MM-DD HH:mm:ss" type="datetime" placeholder=""/>
        </el-form-item>
        <el-form-item label="" prop="goodsIds">
          <el-input v-show="false" v-model="market.goodsIds"/>
          <el-table :data="selectedGoodsList" style="width: 100%;" empty-text="">
            <el-table-column prop="name" label="" width="60">
              <template #default="scope">
                <div style="display: flex;justify-content: center;align-items: center;">
                  <el-image class="goods_image" :src="scope.row.imageUrl"/>
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="title" label="">
              <template #default="scope">
                <div style="font-size: 10px;line-height: 15px;">{{ scope.row.title }}</div>
              </template>
            </el-table-column>
            <el-table-column prop="price" label="" width="80">
              <template #default="scope"> {{ scope.row.price }}</template>
            </el-table-column>
            <el-table-column align="right">
              <template #header>
                <el-popover placement="left" :width="350" trigger="click" :visible="goodsPopoverVisible">
                  <template #reference>
                    <el-button size="small" type="primary" :icon="Plus" @click="addGoods"/>
                  </template>
                  <el-table :data="goodsList" height="30vh" style="width: 100%" @select="selectedGoodsIds">
                    <el-table-column type="selection" width="45"/>
                    <el-table-column prop="name" label="" width="60">
                      <template #default="scope">
                        <div style="display: flex;justify-content: center;align-items: center;">
                          <el-image class="goods_image" :src="scope.row.imageUrl"/>
                        </div>
                      </template>
                    </el-table-column>
                    <el-table-column prop="title" label="">
                      <template #default="scope">
                        <div style="font-size: 10px;line-height: 15px;">{{ scope.row.title }}</div>
                      </template>
                    </el-table-column>
                    <el-table-column prop="price" label="" width="80">
                      <template #default="scope"> {{ scope.row.price }}</template>
                    </el-table-column>
                  </el-table>
                  <div style="text-align: right; margin: 0;padding-top: 10px;">
                    <el-button size="small" @click="goodsPopoverVisible = false"></el-button>
                    <el-button size="small" type="primary" @click="confirmSelectedGoods"></el-button>
                  </div>
                </el-popover>
              </template>
              <template #default="scope">
                <el-button size="small" type="danger" :icon="Minus" @click="removeSelectedGoods(scope.row)" />
              </template>
            </el-table-column>
          </el-table>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="cancel"></el-button>
          <el-button @click="resetForm('ruleForm')"></el-button>
          <el-button type="primary" @click="confirmMarket"></el-button>
        </span>
      </template>
    </el-dialog>
  </container>
</template>

<script>
import container from "@/components/container";
import {Brush, Minus, Plus, Search, Timer, WarningFilled} from "@element-plus/icons-vue";
import {ElMessage} from "element-plus";

export default {
  name: "Market",
  components: {container, Timer},
  setup() {
    return {Search, Brush, Plus, Minus, WarningFilled}
  },
  data() {
    return {
      query: {
        id: '',
        type: '',
        status: ''
      },
      marketList: [],
      goodsList: [],
      market: {
        id: '',
        bannerImage: '',
        name: '',
        type: '',
        beginTime: '',
        overTime: '',
        goodsIds: '',
        status: ''
      },
      typeOption: [{
        value: 1,
        label: ''
      }, {
        value: 2,
        label: ''
      }],

      rules: {
        name: { required: true, message: '', trigger: 'blur' },
        bannerImage: { required: true, message: '', trigger: 'blur' },
        type: { required: true, message: '', trigger: 'blur' },
        beginTime: { required: true, message: '', trigger: 'blur' },
        overTime: { required: true, message: '', trigger: 'blur' },
        goodsIds: { required: true, message: '', trigger: 'blur' }
      },


      // 
      pictureList: [],

      goodsIds: [],
      selectedGoodsList: [],

      marketId: '',

      // 
      total: 0,
      pageNum: 1,
      pageSize: 10,

      // 
      dialogTitle: '',
      operateType: '',
      marketDialogVisible: false,
      goodsPopoverVisible: false,

      // 
      token: '',

      // 
      showEmpty: false
    }
  },
  mounted() {
    this.getMarketList()
    this.token = localStorage.getItem('token')
  },
  methods: {
    // 
    handleCurrentChangePrev(val) {
      this.pageNum = val;
      console.log(`: ${val}`);
    },
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getMarketList()
      console.log(`: ${val}`);
    },
    handleCurrentChangeNext(val) {
      this.pageNum = val;
      console.log(`: ${val}`);
    },

    // 
    handleImagePreview(file) {
      this.market.bannerImage = file.url
    },
    handleImageRemove(file, fileList) {
      this.market.bannerImage = ''
      console.log(file, fileList)
    },
    handleImageSuccess(response) {
      this.market.bannerImage = response.data
    },

    // 
    add() {
      this.dialogTitle = ''
      this.operateType = 'add'
      this.selectedGoodsList = []
      this.marketDialogVisible = true
    },

    // 
    edit(row) {
      this.dialogTitle = ''
      this.operateType = 'edit'
      this.market.id = row.id
      this.market.name = row.name
      this.market.type = row.type
      this.market.bannerImage = row.bannerImage
      this.pictureList.push({url: row.bannerImage})
      this.market.beginTime = row.beginTime
      this.market.overTime = row.overTime
      this.market.goodsIds = row.goodsIds
      this.market.status = row.status
      this.goodsIds = row.goodsIds.split(',')
      this.getSelectedGoodsList(row.goodsIds)
      this.marketDialogVisible = true
    },

    // 
    getMarketList() {
      this.$axios.get('/market/list', {
        params: {
          id: this.query.id,
          type: this.query.type,
          status: this.query.status,
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          sid: parseInt(localStorage.getItem('sid'))
        }
      }).then((response) => {
        this.total = response.data.data.total;
        this.marketList = response.data.data.list;
        if (this.marketList.length === 0) {
          this.showEmpty = true
        }
      }).catch((error) => {
        console.log(error)
      })
    },

    // 
    changeStatus(row){
      console.log(row.status)
      this.$axios.put('/market/status/update', {
        id: row.id,
        status: parseInt(row.status),
      }).then((response) => {
        if (response.data.code === 200 && row.status === 1){
          ElMessage({message: '', type: 'success'})
        }
        if (response.data.code === 200 && row.status === 2){
          ElMessage({message: '', type: 'success'})
        }
      }).catch((error) => {
        console.log(error)
      })
    },

    // 
    addGoods() {
      this.getGoodsList()
      this.goodsPopoverVisible = true
    },

    // ID
    selectedGoodsIds(selection, row) {
      this.goodsIds.push(row.id)
    },

    // 
    confirmSelectedGoods() {
      this.selectedGoodsList = []
      this.market.goodsIds = this.goodsIds.toString()
      this.goodsList.filter(item => {
        this.goodsIds.some(id => { if (id === item.id){ this.selectedGoodsList.push(item) } })
      })
      this.goodsPopoverVisible = false
    },

    // 
    removeSelectedGoods(row){
      this.selectedGoodsList.splice(this.selectedGoodsList.indexOf(row.id), 1)
      this.goodsIds.splice(this.goodsIds.indexOf(row.id), 1)
      this.market.goodsIds = this.goodsIds.toString()
      console.log(this.goodsIds)
    },

    // 
    getGoodsList() {
      this.$axios.get('/market/goods/list', {
        params: { goodsIds: "" }
      }).then((response) => {
        this.goodsList = response.data.data;
      }).catch((error) => {
        console.log(error)
      })
    },

    // 
    getSelectedGoodsList(goodsIds) {
      this.$axios.get('/market/goods/list', {
        params: { goodsIds: goodsIds }
      }).then((response) => {
        this.selectedGoodsList = response.data.data;
      }).catch((error) => {
        console.log(error)
      })
    },

    // 
    confirmMarket() {
      this.$refs['ruleForm'].validate((valid) => {
        if (valid) {
          if (this.operateType === 'add') {
            // 
            this.$axios.post('/market/create', {
              name: this.market.name,
              type: this.market.type,
              bannerImage: this.market.bannerImage,
              beginTime: this.market.beginTime,
              overTime: this.market.overTime,
              goodsIds: this.market.goodsIds,
              sid: parseInt(localStorage.getItem('sid'))
            }).then((response) => {
              if (response.data.code === 200) {
                ElMessage({message: response.data.message, type: 'success'})
              }
              this.getMarketList()
            }).catch((error) => {
              console.log(error)
            })
          }
          if (this.operateType === 'edit') {
            // 
            this.$axios.put('/market/update', {
              id: this.market.id,
              name: this.market.name,
              type: this.market.type,
              bannerImage: this.market.bannerImage,
              beginTime: this.market.beginTime,
              overTime: this.market.overTime,
              goodsIds: this.market.goodsIds,
              status: this.market.status
            }).then((response) => {
              if (response.data.code === 200) {
                ElMessage({message: response.data.message, type: 'success'})
              }
              this.getMarketList()
            }).catch((error) => {
              console.log(error)
            })
          }
          this.marketDialogVisible = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 
    deleteMarket(row) {
      this.$axios.delete('/market/delete', {
          params: {
            id: row.id
          }
        }).then((response) => {
          if (response.data.code === 200) {
            ElMessage({message: response.data.message, type: 'success'})
          }
          this.getMarketList()
        }).catch((error) => {
          console.log(error)
      })
    },

    // 
    cancel() {
      this.$refs['ruleForm'].resetFields()
      this.empty()
      this.marketDialogVisible = false
    },

    // 
    resetForm(formName) {
      this.$refs[formName].resetFields()
      switch (formName) {
        case 'query': this.getMarketList(); break;
        case 'ruleForm': this.empty(); break;
        default: console.log('invalid resetForm');
      }
    },

    // 
    empty() {
      this.market.bannerImage = '',
      this.market.name = '',
      this.market.type = '',
      this.market.beginTime = '',
      this.market.overTime = '',
      this.market.goodsIds = '',
      this.goodsIds = ''
      this.selectedGoodsList = []
      this.pictureList = []
    },
  }
}
</script>

<style scoped>
.goods_image {
  width: 35px;
  height: 35px;
  border-radius: 5px;
}
.el-dialog {
  border-radius: 10px !important;
}

.el-upload{
  width: 100px;
  height: 100px;
  line-height: 100px;
  font-size: 16px;
}

.el-upload--picture-card {
  background-color: #F2F4F7;
  border: none;
}

.el-upload--picture-card{
  width: 100px;
  height: 100px;
  margin-top: 0;
  font-size: 16px !important;
}

.el-upload-list--picture-card .el-upload-list__item{
  width: 100px;
  height: 100px;
  line-height: 100px;
  font-size: 16px;
}

.el-upload-list--picture-card .el-upload-list__item-thumbnail{
  width: 100px;
  height: 100px;
  line-height: 100px;
  font-size: 16px;
}

.el-table__header tr, .el-table__header th {
  background-color: #eff6ff !important;
  font-weight: 450 !important;
  color: #627a94 !important;
  border-bottom: none !important;
}
</style>