<template>
  <container>
    <!--  -->
    <el-form :inline="true" :model="query" ref="query" class="goods_query_form">
      <el-form-item prop="name">
        <el-input v-model.number="query.name" placeholder=""/>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" :icon="Search" @click="getCategoryList"></el-button>
        <el-button type="primary" @click="addLv1Category"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-table :data="categoryLv1List" height="65vh" style="width: 100%">
      <el-table-column prop="id" label=""/>
      <el-table-column prop="name" label=""/>
      <el-table-column prop="level" label=""/>
      <el-table-column prop="sort" label=""/>
      <el-table-column label="" min-width="130">
        <template #default="scope">
          <el-button size="small" type="primary" :icon="Edit" @click="editLv1Category(scope.row)"/>
          <el-button size="small" type="primary" :icon="Plus" @click="addLv2Category(scope.row)"/>
          <el-button size="small" type="primary" @click="checkLv2Category(scope.row)"></el-button>
          <el-popconfirm title=", ?"
                         confirmButtonText=""
                         cancelButtonText=""
                         cancelButtonType="default"
                         :icon="WarningFilled"
                         @confirm="deleteCategory(scope.row)">
            <template #reference>
             <el-button size="small" type="danger" :icon="Delete"/>
            </template>
          </el-popconfirm>
        </template>
      </el-table-column>
      <template #empty>
        <div style="margin: 50px 0;">
          <el-empty v-if="showEmpty" description="" />
        </div>
      </template>
    </el-table>
    <div style="padding: 10px 0;">
      <el-pagination layout="total, prev, pager, next"
                   :current-page="pageNum"
                   :page-size="pageSize"
                   :total="total"
                   @current-change="handleCurrentChange"
                   @prev-click="handleCurrentChangePrev"
                   @next-click="handleCurrentChangeNext" background/>
    </div>
    <!--  -->
    <el-dialog :title="dialogTitle" v-model="categoryDialogVisible" top="30vh" width="35%" @close="cancelCategory">
      <el-form :model="category" label-width="50px" style="padding: 10px;">
        <el-form-item label="">
          <el-input v-model="category.name" type="text" maxlength="10" show-word-limit/>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model.number="category.sort" />
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="cancelCategory"></el-button>
          <el-button type="primary" @click="confirmCategory"></el-button>
        </span>
      </template>
    </el-dialog>
    <!--  -->
    <el-dialog :title="dialogTitle" v-model="checkLv2CategoryDialogVisible" top="10vh" width="50%">
      <el-table :data="categoryLv2List" height="60vh" style="width: 100%">
        <el-table-column prop="id" label=""/>
        <el-table-column prop="name" label=""/>
        <el-table-column prop="level" label=""/>
        <el-table-column prop="sort" label=""/>
        <el-table-column label="" min-width="130">
          <template #default="scope">
            <el-button size="small" type="primary" :icon="Edit" @click="editLv2Category(scope.row)"/>
            <el-popconfirm title=", ?"
                         confirmButtonText=""
                         cancelButtonText=""
                         cancelButtonType="default"
                         :icon="WarningFilled"
                         @confirm="deleteCategory(scope.row)">
            <template #reference>
             <el-button size="small" type="danger" :icon="Delete"/>
            </template>
          </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>
  </container>
</template>

<script>
import container from "../components/container";
import {Delete, Edit, Plus, Search, WarningFilled} from "@element-plus/icons-vue";
import {ElMessage} from "element-plus";

export default {
  name: "Category",
  components: {container},
  setup() {
    return {Search, Plus, Edit, Delete, WarningFilled}
  },
  data() {
    return {
      categoryLv1List: [],
      categoryLv2List: [],
      category: {
        id: '',
        name: '',
        parentId: '',
        level: '',
        sort: ''
      },
      query: {
        name: ''
      },
      dialogTitle: '',
      operateType: '',
      categoryDialogVisible: false,
      checkLv2CategoryDialogVisible: false,

      // 
      total: 0,
      pageNum: 1,
      pageSize: 10,

      // 
      showEmpty: false
    }
  },
  mounted() {
    this.getCategoryList(1)
  },
  methods: {

    // 
    handleCurrentChangePrev(val) {
      this.pageNum = val;
      console.log(`: ${val}`);
    },
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getCategoryList();
      console.log(`: ${val}`);
    },
    handleCurrentChangeNext(val) {
      this.pageNum = val;
      console.log(`: ${val}`);
    },

    // 
    addLv1Category(){
      this.operateType = 'add'
      this.dialogTitle = ''
      this.category.level = 1
      this.category.parentId = 1
      this.categoryDialogVisible = true
    },

    // 
    editLv1Category(row){
      this.operateType = 'edit'
      this.dialogTitle = ''
      this.category.id = row.id
      this.category.name = row.name
      this.category.sort = row.sort
      this.categoryDialogVisible = true
    },

    // 
    addLv2Category(row){
      this.operateType = 'add'
      this.dialogTitle = ''
      this.category.level = 2
      this.category.parentId = row.id
      this.categoryDialogVisible = true
    },

    // 
    editLv2Category(row){
      this.operateType = 'edit'
      this.dialogTitle = ''
      this.category.id = row.id
      this.category.name = row.name
      this.category.sort = row.sort
      this.categoryDialogVisible = true
    },

    // 
    checkLv2Category(row){
      this.dialogTitle = ''
      this.getCategoryList(row.id)
      this.checkLv2CategoryDialogVisible = true
    },

    // 
    getCategoryList(parentId){
      this.$axios.get('/category/list', {
        params: {
          name: this.query.name,
          parentId: parentId,
          sid: localStorage.getItem('sid')
        }
      }).then((response) => {
        if (parentId === 1){
          this.categoryLv1List = response.data.data;
        } else {
          this.categoryLv2List = response.data.data;
        }
        if (this.categoryLv1List.length === 0) {
          this.showEmpty = true
        }
      }).catch((error) => {
        console.log(error)
      })
    },

    // 
    confirmCategory(){
      if (this.operateType === 'add'){
        this.$axios.post('/category/create', {
          name: this.category.name,
          parentId: this.category.parentId,
          level: this.category.level,
          sort: this.category.sort,
          sid: localStorage.getItem('sid')
        }).then((response) => {
          if (response.data.code === 200) {
            ElMessage({message: response.data.message, type: 'success'})
          }
          this.getCategoryList(1)
        }).catch((error) => {
          console.log(error)
        })
      }
      if (this.operateType === 'edit'){
        this.$axios.put('/category/update', {
          id: this.category.id,
          name: this.category.name,
          sort: this.category.sort
        }).then((response) => {
          if (response.data.code === 200) {
            ElMessage({message: response.data.message, type: 'success'})
          }
          this.getCategoryList(1)
        }).catch((error) => {
          console.log(error)
        })
      }
      this.categoryDialogVisible = false
      this.checkLv2CategoryDialogVisible = false
    },
    deleteCategory(row){
      this.$axios.delete('/category/delete', {
          params: {
            id: row.id
          }
        }).then((response) => {
          if (response.data.code === 200) {
            ElMessage({message: response.data.message, type: 'success'})
          }
          this.getCategoryList(1)
        }).catch((error) => {
          console.log(error)
      })
    },
    cancelCategory(){
      this.categoryDialogVisible = false
      this.emptyCategory()
      this.dialogTitle = ''
      this.operateType = ''
    },

    // 
    emptyCategory(){
      this.category.id = ''
      this.category.name = ''
      this.category.parentId = ''
      this.category.level = ''
      this.category.sort = ''
    }
  }
}
</script>

<style scoped>
.el-dialog {
  height: 200px !important;
  overflow-y: scroll;
}
</style>