<template>
  <container>
    <!--  -->
    <el-form :inline="true" :model="query" ref="query" class="goods_query_form">
      <el-form-item prop="id">
        <el-input v-model.number="query.id" placeholder="ID"/>
      </el-form-item>
      <el-form-item prop="status">
        <el-select v-model="query.status" placeholder="">
          <el-option label="" value="1"></el-option>
          <el-option label="" value="2"></el-option>
          <el-option label="" value="3"></el-option>
          <el-option label="" value="4"></el-option>
          <el-option label="" value="5"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" :icon="Search" @click="getOrderList"></el-button>
        <el-button type="primary" :icon="Brush" @click="resetForm"/>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-table :data="orderList" height="65vh" style="width: 100%">
      <el-table-column prop="id" label="" width="200px"/>
      <el-table-column prop="totalPrice" label=""/>
      <el-table-column prop="goodsCount" label="">
        <template #default="scope">{{scope.row.goods_count}}</template>
      </el-table-column>
      <el-table-column prop="status" label="">
        <template #default="scope">
          <el-tag v-if="scope.row.status === 1" size="small" type="info"></el-tag>
          <el-tag v-if="scope.row.status === 2" size="small" type="info"></el-tag>
          <el-tag v-if="scope.row.status === 3" size="small" type="info"></el-tag>
          <el-tag v-if="scope.row.status === 4" size="small" type="primary"></el-tag>
          <el-tag v-if="scope.row.status === 5" size="small" type="success"></el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="created" label="" min-width="150" sortable>
        <template #default="scope">
          <div style="display: flex; align-items: center">
            <el-icon><timer/></el-icon>
            <span style="margin-left: 10px">{{ scope.row.created_time }}</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="" min-width="130">
        <template #default="scope">
          <el-button size="small" @click="checkOrder(scope.$index, scope.row)"></el-button>
          <el-button v-if="scope.row.status === 3" size="small" type="primary" @click="modifyOrder(scope.row, 4)"></el-button>
          <el-popconfirm title=", ?"
                         confirmButtonText=""
                         cancelButtonText=""
                         cancelButtonType="default"
                         v-if="scope.row.status === 2 || scope.row.status === 5"
                         :icon="WarningFilled"
                         @confirm="deleteOrder(scope.row)">
            <template #reference>
             <el-button size="small" type="danger"></el-button>
            </template>
          </el-popconfirm>
        </template>
      </el-table-column>
      <template #empty>
        <div style="margin: 50px 0;">
          <el-empty  v-if="showEmpty" description="" />
        </div>
      </template>
    </el-table>
    <div style="padding: 10px 0;">
      <el-pagination layout="total, prev, pager, next"
                   :current-page="pageNum"
                   :page-size="pageSize"
                   :total="total"
                   @current-change="handleCurrentChange"
                   @prev-click="handleCurrentChangePrev"
                   @next-click="handleCurrentChangeNext" background/>
    </div>
    <!--  -->
    <el-dialog :title="dialogTitle" v-model="orderDialogVisible" top="5vh" width="50%">
      <descriptions label="">{{orderDetail.id}}</descriptions>
      <descriptions label="">
        <el-tag v-if="orderDetail.status === 1" size="small" type="warning"></el-tag>
        <el-tag v-if="orderDetail.status === 2" size="small" type="info"></el-tag>
        <el-tag v-if="orderDetail.status === 3" size="small" type="success"></el-tag>
        <el-tag v-if="orderDetail.status === 4" size="small" type="primary"></el-tag>
        <el-tag v-if="orderDetail.status === 5" size="small" type="success"></el-tag>
      </descriptions>
      <descriptions label="">{{orderDetail.totalPrice}}</descriptions>
      <descriptions label="">
        <el-table :data="orderDetail.goodsItem" style="width: 100%">
          <el-table-column prop="name" label="" width="60">
            <template #default="scope">
              <div style="display: flex;justify-content: center;align-items: center;">
                <el-image class="goods_image" :src="scope.row.pic_url"/>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="title" label="">
            <template #default="scope">
              <div style="font-size: 10px;line-height: 15px;">{{scope.row.name}}</div>
            </template>
          </el-table-column>
          <el-table-column prop="count" label="" width="80">
            <template #default="scope">x {{scope.row.count}}</template>
          </el-table-column>
        </el-table>
      </descriptions>
      <descriptions label="">{{orderDetail.name}}</descriptions>
      <descriptions label="">{{orderDetail.tel}}</descriptions>
      <descriptions label="">{{orderDetail.province + ' ' + orderDetail.city + ' ' + orderDetail.district +
      ' ' + orderDetail.detailedAddress }}</descriptions>
      <descriptions label="">{{orderDetail.created}}</descriptions>
      <template #footer>
        <span class="dialog-footer">
          <el-button type="primary" @click="orderDialogVisible = false"></el-button>
        </span>
      </template>
    </el-dialog>
  </container>
</template>

<script>
import container from "../components/container";
import {Brush, Delete, Search, Setting, Timer, WarningFilled} from "@element-plus/icons-vue";
import {ElMessage} from "element-plus";
import descriptions from "@/components/descriptions";

export default {
  name: "Order",
  components: {descriptions, container, Timer},
  setup() {
    return {Search, Brush, Setting, Delete, WarningFilled}
  },
  data() {
    return {
      orderList: [],
      order: {
        id: '',
        amount: '',
        goodsCount: '',
        status: '',
        shippingAddress: ''
      },
      query: {
        id: '',
        status: ''
      },
      orderDetail: {
        id: '',
        status: '',
        totalPrice: '',
        goodsItem: [],
        name: '',
        mobile: '',
        province: '',
        city: '',
        district: '',
        detailedAddress: '',
        created: '',
        nickName: ''
      },
      dialogTitle: '',
      operateType: '',
      orderDialogVisible: false,

      // 
      total: 0,
      pageNum: 1,
      pageSize: 12,

      // 
      showEmpty: false
    }
  },
  mounted() {
    this.getOrderList()
  },
  methods: {
    // 
    handleCurrentChangePrev(val) {
      this.pageNum = val;
      console.log(`: ${val}`);
    },
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getOrderList();
      console.log(`: ${val}`);
    },
    handleCurrentChangeNext(val) {
      this.pageNum = val;
      console.log(`: ${val}`);
    },

    // 
    getOrderList(){
      this.$axios.get('/order/list', {
        params: {
          id: this.query.id,
          status: this.query.status,
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          sid: parseInt(localStorage.getItem('sid'))
        }
      }).then((response) => {
        this.total = response.data.data.total
        this.orderList = response.data.data.list
        if (this.orderList.length === 0) {
          this.showEmpty = true
        }
      }).catch((error) => {
        console.log(error)
      })
    },

    // 
    checkOrder(index, row) {
      this.orderDetail.nickName = row.nickName
      console.log(row.id)
      this.dialogTitle = ''
      this.$axios.get('/order/detail', {
        params: { id: row.id }
      }).then((response) => {
        this.orderDetail.id = response.data.data.id
        this.orderDetail.status = response.data.data.status
        this.orderDetail.totalPrice = response.data.data.totalPrice
        this.orderDetail.goodsItem = response.data.data.goodsItem
        this.orderDetail.name = response.data.data.name
        this.orderDetail.mobile = response.data.data.mobile
        this.orderDetail.province = response.data.data.province
        this.orderDetail.city = response.data.data.city
        this.orderDetail.district = response.data.data.district
        this.orderDetail.detailedAddress = response.data.data.detailedAddress
        this.orderDetail.created = response.data.data.created
      }).catch((error) => {
        console.log(error)
      })
      this.orderDialogVisible = true
    },

    // 
    modifyOrder(row, status) {
      this.$axios.put('/order/update', {
        id: row.id,
        status: status
      }).then((response) => {
        if (response.data.code === 200 && status === 4){
          ElMessage({message: '', type: 'success'})
        }
        this.getOrderList()
      })
    },

    // 
    resetForm() {
      this.$refs['query'].resetFields()
      this.getOrderList()
    },

    // 
    deleteOrder(row){
      this.$axios.delete('/order/delete', {
          params: {
            id: row.id
          }
        }).then((response) => {
          if (response.data.code === 200) {
            ElMessage({message: response.data.message, type: 'success'})
          }
          this.getOrderList()
        }).catch((error) => {
          console.log(error)
      })
    }
  }
}
</script>

<style scoped>
.goods_image{
  width: 35px;
  height: 35px;
  border-radius: 5px;
}
.el-dialog {
  border-radius: 10px !important;
}

.el-table__header tr, .el-table__header th {
  background-color: #eff6ff !important;
  font-weight: 450 !important;
  color: #627a94 !important;
  border-bottom: none !important;
}
</style>