import { createStore } from 'vuex'

const store = createStore({
    state() {
        return {
            
            // 
            defaultActive: '/index',

            // 
            navigation: [{
                name: '',
                index: '/index',
            }],

            // 
            pageTitle: ''
        }
    },
    mutations: {
        setMenu(state, index){
            state.defaultActive = index
        },
        addNav(state, item) {
            state.navigation = item
        },
        setPageTitle(state, title) {
            state.pageTitle = title
        }
    },
    actions: {},
    modules: {}
})

export default store;